body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    position: relative;
    min-height: 100%;
}

main {
    min-height: calc(100vh - 94px);
    margin-bottom: -30px;
}

#main h2.users {
    color: #868688;
    margin: 20px 0;
}

#sort-holder {
    margin-bottom: 30px;
}

#sort-holder label {
    position: relative;
    left: 10px;
}

#collection {
    margin-bottom: 80px;
}

#pagination {
    margin: 0 auto;
    display: table;
}

.loading-holder {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: calc(100vh - 75px);
}

.loading-holder img {
    align-self: center;
}

li.transparent:hover {
    border: 1px solid #999999;
    border-bottom: 1px solid #999999 !important;
}

.transparent {
    width: 100%;
    height: 100%;
    z-index: 3;
    cursor: pointer;
}

.transparent .delete-button {
    width: 35px;
    height: 35px;
}

.transparent .delete-button i {
    line-height: 35px;
}

.modal-holder {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 3333333;
    height: 100%;
}

#modal {
    display: block;
    opacity: 1;
    top: 20%;
    -webkit-transform: scaleX(1) scaleY(1);
            transform: scaleX(1) scaleY(1);
    z-index: 333333333;
    overflow-y: visible;
    overflow-y: initial;
    min-height: 450px;
}

#create-user-main {
    min-height: calc(100vh - 74px);
}
#create-user-main .container {
    min-height: 530px;
}

#create-user-main form {
    position: relative;
    top: 80px;
}

.error-msg {
    color: red;
}

#new-user-button-holder {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    height: 92px;
}

#main-footer {
    width: 100%;
    color: #fff;
    padding: 0;
    height: 40px;
}

#main-footer small {
    line-height: 40px;
}

.datepicker-title {
    color: rgb(38, 166, 154);
}

.calendar-holder {
    display: flex;
    justify-content: center;
    align-items: center
}

#main-login {
    height: 100vh;
    display: flex;
    align-items: center;
}

#main-login label {
    font-size: 1.3rem;
}

#main-login i {
    color: #757575;
    margin-top: 5px;
}

.login-holder {
    margin: 0;
}

#login-card {
    padding: 10px 30px;
}

.login-title-holder {
    margin-bottom: 0;
}

.register-button {
    font-weight: bold;
    cursor: pointer;
}

@media only screen and (min-width: 401px) {
    #mobile-demo {
        width: 300px;
    }
}

@media only screen and (max-width: 600px) {
    main {
        min-height: calc(100vh - 85px);
    }
}

@media only screen and (max-width: 400px) {
    #mobile-demo {
        width: 230px;
    }

    .modal-btns {
        width: 100%;
        margin-bottom: 10px;
    }
}
